@-webkit-keyframes icon-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

@keyframes icon-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

.icon-spin {
  -webkit-animation: icon-spin 2s infinite linear;
  animation: icon-spin 2s infinite linear;
}

html {
  font-size: 16px;
}

html,
body {
  /* position: fixed; */
  height: 100%;
  width: 100%;
}

#modal-root,
#error-modal {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 999;
  display: flex;
  align-items: stretch;
  justify-content: center;
}

.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  width: 100vw;
  height: 100%;
  justify-content: center;
  align-items: stretch;
}

input.tms-input {
  font-family: ProximaNova-Semibold, Lato, sans-serif;
  font-size: 1.1rem;
  border: none;
}

input.tms-input:focus {
  outline: none;
}

body {
  font-family: ProximaNova-Semibold, sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  overflow: hidden;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  flex-direction: column;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  -webkit-transition: 300ms linear;
  transition: 300ms linear;
  opacity: 1 !important;
}

.fade-enter-done {
  opacity: 1 !important;
}

.fade-exit {
  opacity: 1 !important;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: 3000ms linear;
  transition: 3000ms linear;
}

.fade-in-enter,
.fade-in-appear {
  -webkit-transform: translateY(2rem);
          transform: translateY(2rem);
  opacity: 0;
}

.fade-in-enter.fade-in-enter-active,
.fade-in-appear.fade-in-appear-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: none;
          transform: none;
  opacity: 1;
}

.slide-next-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.slide-next-enter.slide-next-enter-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: unset;
          transform: unset;
}

.slide-next-exit {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.slide-next-exit.slide-next-exit-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: translate(100%, -100%);
          transform: translate(100%, -100%);
}

.slide-previous-enter {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.slide-previous-enter.slide-previous-enter-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: unset;
          transform: unset;
}

.slide-previous-exit {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-previous-exit.slide-previous-exit-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: translateX(-100%) scale(0.6);
          transform: translateX(-100%) scale(0.6);
}

.fly-in-enter,
.fly-in-appear {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}

.fly-in-enter.fly-in-enter-active,
.fly-in-appear.fly-in-appear-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: none;
          transform: none;
}

.fly-in-exit {
  -webkit-transform: none;
          transform: none;
}

.fly-in-exit.fly-in-exit-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}

.slide-in-enter,
.slide-in-appear {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.slide-in-enter.slide-in-enter-active,
.slide-in-appear.slide-in-appear-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: none;
          transform: none;
}

.slide-in-exit {
  -webkit-transform: none;
          transform: none;
}

.slide-in-exit.slide-in-exit-active {
  -webkit-transition: 200ms linear;
  transition: 200ms linear;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
